import React, { Component } from 'react';
import Editor from 'react-simple-code-editor';
import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import Multiselect from 'multiselect-dropdown-react';
const code = `









`;
const data = [{
  name: 'orderID',
  value: 'orderID'
},
{
    name: 'productID',
    value: 'productID'
  },
  {
    name: 'productName',
    value: 'productName'
  },
  {
    name: 'customerID',
    value: 'customerID'
  },
  {
    name: 'employeeID',
    value: 'employeeID'
  },
  {
    name: '*',
    value: '*'
  }];
const tables =   [{
  name: 'Citizens',
  value: 'Citizens'
},
{
    name: 'Districts',
    value: 'Districts'
  },
  {
    name: 'Cities',
    value: 'Cities'
  },
  {
    name: 'Markets',
    value: 'Markets'
  }];
class CodeEditor extends Component {
  state = { code };
  result = (e) => {
    this.setState({
      code: `SELECT ${e.toString()}`
    })
  }
  appendQuery =(e) => {
    this.setState({
      code: `${this.state.code} FROM ${e.toString()}`
    })
  }
  render() {
    return (
      <div><div className='table-header'><h3>Query Composer</h3>
       <div className='button-container'><button className="btn"><i className="fa fa-play"></i> Run</button>
       <button className="btn"><i className="fa fa-floppy-o"></i> Save View</button>
       <button className="btn"><i className="fa fa-trash"></i> Clear</button></div></div>
      <div className='editor-divide'> <div className='query-pad'><h3>Query Editor</h3>  <Editor
        value={this.state.code}
        onValueChange={code => this.setState({ code })}
        highlight={code => highlight(code, languages.js)}
        padding={10}
        style={{
          fontFamily: '"Fira code", "Fira Mono", monospace',
          fontSize: 12,
        }}/> </div> <div className='query-pad'><h3>Query constructor</h3> 
        <div className='query-edit-ui'>
        <div><div className='queryHeading'>SELECT</div>
        <Multiselect options={data} onSelectOptions={this.result} /></div>
        
        <div><div className='queryHeading'>FROM</div>
        <Multiselect options={tables} onSelectOptions={this.appendQuery} /></div>
        <div><div className='queryHeading'>WHERE</div>
        <Multiselect options={data} onSelectOptions={this.result} /></div>
       </div>
        </div>

        
        </div> 
    </div>
    );
  }
}

export default CodeEditor;