import React, { Component } from 'react';
import './queryInterface.css';
import dataSets from '../Mocks/dataSets.json';
import dataSetsTable from '../Mocks/dataSetsTable.json';
import TableView from '../Components/tableView';

class QueryInterface extends Component {
  state = {
    showTableView: false
  }
  componentDidMount = () => {

  }
  showtableView = () => {
    this.setState({showTableView: true})
  }
  render() {
    const listOfDirectories = dataSets.map(function(item){
      return  <li className="folder" key={item.name}>{item.name}
            {dataSetsTable.map(function(item){
     return <ul key={item.name}><li><a onClick={this.showtableView}>{item.name}</a></li></ul>
    }, this)}
      </li>
    }, this)
    return (
      <div className="tree-container">
     { !this.state.showTableView && <div className='directory'><div className='page-heading'>Your datasets</div>
      <div className="list">
        <ul className="directory-list">
        {listOfDirectories}
        </ul>
      </div>
      </div>}
      <div className='data-view'></div>
      {this.state.showTableView &&<TableView/>}
      </div>
    );
  }
}

export default QueryInterface;
